<template>
  <div class="createapply-formgroup-content">
    <div class="formgroup-approverList">
      <div class="formgroup-approverItem" v-for="(item,index) in approve">
        <div class="approverItem-icon-check">
          <span v-if="item.status==0||item.status_str==''"></span>
          <img v-else src="../../images/apply_approver.png">
        </div>
        <div class="formgroup-approverItem-right">
          <div class="formgroup-approverItem-right-top">
            <div class="approverItem-text">
              {{item.position}}
            </div>
            <div class="approverItem-namelist">
              <div class="approverItem-nameitem">
                <img :src="item.url" alt="" @error="getImgurl">
                <span>{{item.name}}</span>
              </div>
            </div>
            <div class="approverItem-status">
  
              <span v-if="item.sign_img" class="approverItem-status-sign-img" @click="alertSignImg(item.sign_img)"><img :src="item.sign_img" alt="" > </span>
  
              <span class="approverItem-status-span" v-if="item.status==1">
                  &nbsp;&nbsp;
                  <i class="viewapply-icon icon-agree"></i>
                </span>
              <span class="approverItem-status-span" v-if="item.status_str==''">
                
                <i class="viewapply-icon icon-agree"></i>
              </span>
              <span class="approverItem-status-span" v-if="item.status_str==''">
                
                <i class="viewapply-icon icon-agree"></i>
              </span>
              <span class="approverItem-status-span" v-if="item.status==2">
                  &nbsp;&nbsp;
                  <i class="viewapply-icon icon-reject"></i>
                </span>
              <span class="approverItem-status-span" v-if="item.status==3&&aType==2">
                  
                  <i class="viewapply-icon icon-cxtj"></i>
                </span>
              <span class="approverItem-status-span" v-if="item.status==3&&aType==3">
                  
                  <i class="viewapply-icon icon-cxtj"></i>
                </span>
              <span class="approverItem-status-span" v-if="item.status==3&&aType==1">
                  
                  <i class="viewapply-icon icon-cxtj"></i>
                </span>
              <span class="approverItem-status-span" v-if="item.status==4">
                  
                  <i class="viewapply-icon icon-cxsq"></i>
                </span>
              <span class="approverItem-status-span" v-if="item.status==5">
                  &nbsp;&nbsp;
                  <i class="viewapply-icon icon-cxtj"></i>
                </span>
              <span class="approverItem-status-span" v-if="item.status==6">
                  
                  <i class="viewapply-icon icon-cxtj"></i>
                </span>
              <span class="approverItem-status-span approverItem-status-span1" v-if="item.is_edit_file==1&&aType==2">
                  
                </span>
              <span class="approverItem-status-span" v-if="item.is_edit_file==0&&item.status==5&&aType==2">
                  
                </span>
            </div>
            <div v-if="item.status==0||item.status_str==''||item.status_str==''" class="approverItem-time">
              {{item.status_str}}
            </div>
            <div v-if="item.status!=0" class="approverItem-time">
              {{item.approval_time}}
            </div>
          </div>
          <div class="formgroup-approverItem-right-bottom" v-if="item.showOpinion">
            <div class="approverItem-opinionbox">
              <span></span>
              <div class="approverItem-opinion-text">{{item.content}}</div>
            </div>
            <div class="approverItem-opinionbox" v-if="item.attach!=undefined&&item.attach.length!=0">
              <span></span>
              <div class="viewapply-content-item-imglist">
                <div class="viewapply-content-item-imgItem" v-for="item in item.attach">
                  <i class="filemanage-icon" :class="'icon-type-'+item.typestring"></i>
                  <div><a :href="item.url" target="_blank" download="">{{item.name}}</a></div>
                </div>
              </div>
            </div>
            <div class="approverItem-comments-total" v-if="!item.showComment"></div>
            <div class="approverItem-comments-total" v-if="item.showComment&&item.comment.length!=0">{{item.comment.length}}</div>
            <div class="approverItem-commentslist" v-if="item.showComment&&item.showall">
              <div class="approverItem-commentsitem" v-for="(item1,index1) in item.comment" v-if="index1<3">
                <img class="commentsitem-img" :src="item1.url" @error="getImgurl">
                <div class="approverItem-commentsitem-content">
                  <div class="commentsitem-name" v-if="item1.reply!=''">
                    <em>{{item1.name}}</em> 
                    <em>{{item1.reply}}</em>
                  </div>
                  <div class="commentsitem-name" v-else>
                    <em>{{item1.name}}</em>
                  </div>
                  <div class="commentsitem-content">{{item1.content}}</div>
                  <div class="commentsitem-time">{{item1.add_time}}
                    <div class="commentsitem-time-btn" @click="replyclick(item1.comment_id,index,item1.name)">
                      <img src="../../images/apply_icon_comment.png">
                    </div>
                  </div>
                </div>
              </div>
            </div>
            <div class="approverItem-commentslist" v-if="item.showComment&&!item.showall">
              <div class="approverItem-commentsitem" v-for="(item1,index1) in item.comment">
                <img class="commentsitem-img" :src="item1.url" @error="getImgurl">
                <div class="approverItem-commentsitem-content">
                  <div class="commentsitem-name" v-if="item1.reply!=''">
                    <em>{{item1.name}}</em> 
                    <em>{{item1.reply}}</em>
                  </div>
                  <div class="commentsitem-name" v-else>
                    <em>{{item1.name}}</em>
                  </div>
                  <div class="commentsitem-content">{{item1.content}}</div>
                  <div class="commentsitem-time">{{item1.add_time}}
                    <div class="commentsitem-time-btn" @click="replyclick(item1.comment_id,index,item1.name)">
                      <img src="../../images/apply_icon_comment.png">
                    </div>
                  </div>
                </div>
              </div>
            </div>
            <div class="approverItem-loadingbox" v-if="item.showComment&&item.comment.length>3" @click="changeShowall(index)">
              <em v-if="item.showall"></em>
              <em v-if="!item.showall"></em>
            </div>
            <div class="approverItem-commentbox">
              <el-input size="mini" class="approverItem-commentbox-input" clearable v-model="item.newComment.content" :placeholder="item.newComment.placeholder"></el-input>
              <div class="approverItem-commentbox-textbtn" v-if="item.newComment.comment_id!=0" @click="cancelReply(index)"></div>
              <div class="approverItem-commentbox-btn" @click="createCommentFun(item.newComment.id,item.newComment.comment_id,item.newComment.content,index)"></div>
            </div>
          </div>
        </div>
      </div>
    </div>
    <div class="formgroup-discussList" v-if="type=='approveaaaa'">
      <div class="approverItem-comments-total" v-if="discuss.discusslist.length==0"></div>
      <div class="approverItem-comments-total" v-if="discuss.discusslist.length!=0">{{discuss.discusslist.length}}</div>
      <div class="approverItem-commentslist" v-if="discuss.discusslist.length!=0&&discuss.showall">
        <div class="approverItem-commentsitem" v-for="(item1,index1) in discuss.discusslist" v-if="index1<3">
          <img class="commentsitem-img" :src="item1.url" @error="getImgurl">
          <div class="approverItem-commentsitem-content">
            <div class="commentsitem-name">
              <em>{{item1.name}}</em>
            </div>
            <div class="commentsitem-content">{{item1.message}}</div>
            <div class="commentsitem-time">{{item1.add_time}}
            </div>
          </div>
        </div>
      </div>
      <div class="approverItem-commentslist" v-if="discuss.discusslist.length!=0&&!discuss.showall">
        <div class="approverItem-commentsitem" v-for="(item1,index1) in discuss.discusslist">
          <img class="commentsitem-img" :src="item1.url" @error="getImgurl">
          <div class="approverItem-commentsitem-content">
            <div class="commentsitem-name">
              <em>{{item1.name}}</em>
            </div>
            <div class="commentsitem-content">{{item1.message}}</div>
            <div class="commentsitem-time">{{item1.add_time}}
            </div>
          </div>
        </div>
      </div>
      <div class="approverItem-loadingbox" v-if="discuss.discusslist.length>3" @click="changediscussShowall()">
        <em v-if="discuss.showall"></em>
        <em v-if="!discuss.showall"></em>
      </div>
      <!--       <div class="approverItem-commentbox">
          <el-input size="mini" class="approverItem-commentbox-input" clearable v-model="discuss.content" placeholder=""></el-input>
          <div class="approverItem-commentbox-btn" @click="createDiscuss(discuss.content)"></div>
        </div> -->
    </div>

    <el-dialog title="" :visible.sync="showEwm" width="400px" center :append-to-body='true'  class="wxgzh">
      <div class="wxgzh_img_box">
        <img :src="ewmSrc" alt="">
      </div>
    </el-dialog>
  </div>
</template>

<script>
  import {
    dialogConfirm,
    compareJson,
    dialogConfirmHasbtn
  } from '../../config/mUtils'
  export default {
    data() {
      return {
        showEwm:false,
        ewmSrc:'',
      }
    },
    mounted() {},
    created() {},
    computed: {},
    props: ['approve', 'discuss', 'type', 'aType'],
    methods: {
      getImgurl(e) {
        e.currentTarget.src = '../../static/images/header_userimg.png';
        e.onerror = null;
      },
      createCommentFun(hid, cid, content, cindex) {
        if (content == '') {
          dialogConfirm('info', '')
        } else {
          let param = {
            param: {
              hid: hid,
              comment_id: cid,
              content: content
            },
            cindex: cindex
          }
          this.$emit('createCommentFun', param);
        }
      },
      //
      replyclick(cid, cindex, rname) {
        let param = {
          cindex: cindex,
          comment_id: cid,
          replier: rname,
          cindex: cindex
        }
        this.$emit('createreplyCommentFun', param)
      },
      //
      cancelReply(cindex) {
        let param = {
          cindex: cindex
        }
        this.$emit('cancelReplyFun', param)
      },
      changeShowall(cindex) {
        let param = {
          cindex: cindex
        }
        this.$emit('changeShowallFun', param)
      },
      changediscussShowall() {
        this.$emit('changediscussShowall')
      },
      //
      createDiscuss(e) {
        if (e == '') {
          this.dialogConfirm('info', '')
        } else {
          this.$emit('createDiscuss', e)
        }
      },
      //
      alertSignImg(imgSrc){
        this.ewmSrc = imgSrc;
        this.showEwm =true;
      }
    }
  }
</script>

<style lang="scss" scoped>
  @import '../../style/mixin';
  @import '../../style/apply';
  .createapply-formgroup-content {
    margin-left: 0px;
  }
  
  .viewapply-content-item-imglist {
    .viewapply-content-item-imgItem {
      margin-bottom: 10px;
    }
  }
  
  .approverItem-status-sign-img {
    width: 50px;
    height: 24px;
    cursor: pointer;
    border: 1px solid #DFDEDE;
    img {
      width: 100%;
      height: 22px;
    }
  }
  // .wxgzh_img_box{
  //   img{
  //     width: 100%;
  //   }
  // }
  .wxgzh .wxgzh_img_box{
    text-align: center;
  }
  .wxgzh  .wxgzh_img_box{
  width: 350px;
  height: 350px;
  display: flex;
  justify-content: center;
  align-items: center;
}
.wxgzh  .wxgzh_img_box img{
  width: 100%;
}
</style>



